.overlay {
   display: none;
   position: fixed;
   width: 100%;
   height: 100%;
   top: 0;
   background-color: rgba(0, 0, 0, 0.56);
}
.modal {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   width: 380px;
   min-height: 400px;
   background-color: #f2f2f2;
   padding: 38px 40px 40px 40px;
   &__close {
      position: absolute;
      top: 0;
      right: -33px;
      font-size: 41px;
      line-height: 21px;
      color: $color_red;
      cursor: pointer;
   }
   &__subtitle {
      color: $color_black;
      font-size: 18px;
      font-weight: 700;
      text-align: center;
   }
   &__descr {
      margin-top: 5px;
      color: $color_black;
      font-size: 14px;
      font-weight: 500;
      text-align: center;
   }
   &_mini {
      min-height: 140px;
   }
   //Error
   .error {
      border: 1px solid red;
   }
   label.error {
      border: none;
      text-align: center;
      margin-bottom: 15px;
   }
}

#consultation, #order, #thanks {
   display: none;
}