.feed-form { 
   margin-top: 36px;
   display: flex;
   flex-direction: column;
   max-width: 300px;
   input {
      height: 50px;
      border-radius: 5px;
      background-color: #fff;
      margin-bottom: 15px;
      font-size: 14px;
      font-weight: 300;
      color: #4b4b4b;
      padding: 0 20px;
      &:nth-child(3) {
         margin-bottom: 20px;
      }
   }
   &_mt25 {
      margin-top: 25px;
   }
}
