.carousel {
   padding: 81px 0;
   &__inner {
      max-width: 750px;
      margin: 0 auto;
      position: relative;
   }
   .slick-prev, .slick-next {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: -66px;
      background-color: #fff;
      img {
         width: 31px;
         height: 50px;
      }
   }
   .slick-next {
      left: auto;
      right: -66px;
   }
}
