.button { 
   width: 150px;
   height: 30px;
   border-radius: 5px;
   background-color: $color_red;
   font-size: 12px;
   font-weight: 500;
   text-align: center;
   color: #fff;
   text-transform: uppercase;
   &_main {
      width: 300px;
      height: 50px;
      font-size: 16px;
   }
   &_submit {
      width: 100%;
      height: 55px;
      font-size: 16px;
   }
   &_catalog {
      width: 131px;
      height: 40px;
      font-size: 14px;
   }
   @include btn_hover
}
